}

.all-task {
  display: flex;
  border-bottom: 1px solid #d7d7d7;
  padding-bottom: 0.3rem;
  padding-top: 0.3rem;
}
.left-img {
  width: 30%;
}
.left-img > img {
  width: 100%;
  padding-right: 0.4rem;
}
.right {
  width: 70%;
}
.right-top {
  justify-content: space-between;
  padding: 0.3rem 0.4rem;
  border-bottom: 1px solid #d7d7d7;
  color: #000;
}
.right-top-child {
  display: flex;
  align-items: center;
}
.right-top-childImg {
  width: 25px;
  padding-right: 5px;
}
.right-center {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 0.4rem;
}
.right-center > p {
  font-size: 1.2rem;
  font-weight: bold;
  color: #1890ff;
}
.right-bottom {
  display: inline;
  justify-content: space-between;
  font-size: 1rem;
  color: #000;
}
.paddingBottom {
  padding-bottom: 0.1rem;
}
.right-bottom > p > span:nth-child(2) {
  color: #007aff;
  margin-right: 0.4rem;
}
.taskss {
  color: red;
}
.taskLists {
  padding: 0.4rem 0.6rem;
  background-color: #fbdbdb;
  color: #f30;
  font-size: 1rem;
}

.buyAdmin-box {
  width: 100%;
  padding: 3.4rem 0.6rem 0 0.6rem;
  background-color: #fff;
}
.buyAdmin-title {
  padding-bottom: 1rem;
  color: red;
  font-size: 1rem;
  font-weight: bold;
}
.buy-input {
  width: 100%;
}
.look-shilitu {
  display: flex;
  justify-content: space-between;
  color: red;
  font-weight: bold;
  font-size: 1rem;
  padding: 0 1.5rem;
  margin-bottom: 2.5rem;
}
.btn-buy {
  position: relative;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3rem;
  margin-bottom: 1rem;
}
.buyAdmin-box > ul > li {
  margin-bottom: 0.8rem;
  border-bottom: 1px solid #e7e7e7;
}
a {
  color: rgba(0, 0, 0, 0.65);
}
.bind-list {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 0;
}
.bind-list > div > img {
  padding-right: 0.5rem;
}
.bind-content {
  color: red;
  margin-left: 2rem;
}

.bodyBox {
  padding: 0.5rem 0.5rem;
  background-color: #fff;
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
}
.childOne {
  width: 80%;
  margin-right: 5%;
}
.childOne_a {
  font-size: 1rem;
  display: flex;
  justify-content: space-between;
}
.childOne_as {
  color: red;
  font-size: 0.7rem;
  display: flex;
  justify-content: space-between;
}
/* .childOne > div > span:first-child {
  color: #000;
  font-size: 1rem;
}
.childOne > div > span:last-child {
  padding-left: 2rem;
} */
.childOne > div > span {
  font-size: 0.9rem;
}
.childTwo {
  width: 15%;
  text-align: center;
  padding: 0.2rem 0.1rem;
  background-color: #f0ad4e;
  color: #fff;
  border-radius: 10px;
  font-size: 12px;
}

.tongZhi-box {
  padding-top: 3.5rem;
}
.tongZhi-box > li {
  border-top: 1px solid #d7d7d7;
  border-bottom: 1px solid #d7d7d7;
  background-color: #fff;
  margin-bottom: 0.5rem;
}
.tongZhi-box > li > div {
  padding: 0.6rem 0.8rem;
  color: #333;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tongZhi-box > li > div > span:last-child {
  display: block;
  padding: 0.2rem 0.4rem;
  background-color: rgba(0,0,0,.15);
  border-radius: 12px;
  font-size: 0.8rem;
}

/* 公告详情 */
.tongZhiChild {
  padding: 4rem 0.7rem 0 0.7rem;
}
.tongZhiChild > h3 {
  text-align: center;
  padding-bottom: 1rem;
}
.tongZhiChild > p {
  padding-bottom: 0.7rem;
}
.tongZhiChild > img {
  width: 100%;
  padding-bottom: 0.7rem;
}

.loadings {
  padding-top: 50%;
}

